import { toString, Union } from "./fable_modules/fable-library.3.7.9/Types.js";
import { union_type } from "./fable_modules/fable-library.3.7.9/Reflection.js";
import { ofArray } from "./fable_modules/fable-library.3.7.9/List.js";

export class Page extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["Overview", "Installation", "Philosophy", "BasicUsage", "ConditionalStyling", "Pseudo", "Composition", "Labels", "Transition", "KeyframesAnimations", "Combinators", "MediaQueries", "GlobalStyles", "Counters", "Fonts", "BackgroundImages", "Core", "Fable", "Feliz", "Giraffe", "Svg", "Troubleshoot", "Unknown", "NotFound"];
    }
}

export function Page$reflection() {
    return union_type("Pages.Page", [], Page, () => [[], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], [], []]);
}

export const allPages = ofArray([new Page(0), new Page(1), new Page(2), new Page(3), new Page(4), new Page(5), new Page(6), new Page(7), new Page(8), new Page(9), new Page(10), new Page(11), new Page(12), new Page(13), new Page(14), new Page(15), new Page(16), new Page(17), new Page(18), new Page(19), new Page(20), new Page(21)]);

