import { fold } from "./fable_modules/fable-library.3.7.9/Seq.js";
import { isUpper } from "./fable_modules/fable-library.3.7.9/Char.js";
import { printf, toText } from "./fable_modules/fable-library.3.7.9/String.js";

export function combine(separator, value) {
    return fold((acc, element) => {
        if (isUpper(element) && (acc.length === 0)) {
            return acc + element;
        }
        else if (isUpper(element) && (acc.length !== 0)) {
            return toText(printf("%s%s%s"))(acc)(separator)(element);
        }
        else {
            return acc + element;
        }
    }, "", value.split(""));
}

